const dotenv = require('dotenv');
const path = require('path');

// Load environment variables
dotenv.config({ path: path.join(__dirname, '..', '.env') });

// Validate required environment variables
const requiredEnvVars = [
  'POSTGRES_DATABASE_URL',
  'POSTGRES_JWT_SECRET',
  'REDIS_URL',
  'REDIS_TTL'
];

const missingEnvVars = requiredEnvVars.filter(varName => !process.env[varName]);

if (missingEnvVars.length > 0) {
  console.error(`âŒ Missing required environment variables: ${missingEnvVars.join(', ')}`);
  process.exit(1);
}

// Database configuration
const databaseConfig = {
  url: process.env.POSTGRES_DATABASE_URL,
  ssl: process.env.POSTGRES_SSL === 'true',
  connectionTimeoutMillis: 10000,
  queryTimeoutMillis: 30000,
  idleTimeoutMillis: 30000,
  max: 20
};

// JWT configuration
const jwtConfig = {
  secret: process.env.POSTGRES_JWT_SECRET,
  expiresIn: process.env.JWT_EXPIRES_IN || '24h',
  refreshExpiresIn: process.env.JWT_REFRESH_EXPIRES_IN || '7d'
};

// Redis configuration
const redisUrl = process.env.REDIS_URL;
const redisTtl = parseInt(process.env.REDIS_TTL) || 3600;

// Parse Redis URL for connection details
const redisUrlPattern = /^redis:\/\/([^:]+):([^@]+)@([^:]+):(\d+)(?:\/(.*))?$/;
const match = redisUrl.match(redisUrlPattern);

let redisConfig;
if (match) {
  // ACL-style authentication: redis://username:password@host:port
  redisConfig = {
    host: match[3],
    port: parseInt(match[4]),
    username: match[1],
    password: match[2],
    db: match[5] ? parseInt(match[5]) : 0,
    retryDelayOnFailover: 100,
    maxRetriesPerRequest: 3,
    lazyConnect: true,
    keepAlive: 30000,
    connectTimeout: 10000,
    commandTimeout: 5000,
    maxmemoryPolicy: 'allkeys-lru'
  };
} else {
  // Fallback for simple redis://host:port format
  const urlParts = redisUrl.replace('redis://', '').split(':');
  redisConfig = {
    host: urlParts[0],
    port: parseInt(urlParts[1]) || 6379,
    retryDelayOnFailover: 100,
    maxRetriesPerRequest: 3,
    lazyConnect: true,
    keepAlive: 30000,
    connectTimeout: 10000,
    commandTimeout: 5000,
    maxmemoryPolicy: 'allkeys-lru'
  };
}

// Cache configuration
const cacheConfig = {
  redis: redisConfig,
  ttl: redisTtl,
  keyPrefix: 'smarttech:',
  defaultCacheOptions: {
    ttl: redisTtl,
    updateAgeOnGet: false
  }
};

// Email configuration
const emailConfig = {
  host: process.env.SMTP_HOST,
  port: parseInt(process.env.SMTP_PORT) || 587,
  secure: process.env.SMTP_SECURE === 'true',
  auth: {
    user: process.env.SMTP_USER,
    pass: process.env.SMTP_PASS
  },
  from: process.env.EMAIL_FROM || 'noreply@smarttech.com'
};

// SMS configuration
const smsConfig = {
  apiKey: process.env.SMS_API_KEY,
  apiSecret: process.env.SMS_API_SECRET,
  sender: process.env.SMS_SENDER || 'SmartTech'
};

// Server configuration
const serverConfig = {
  port: parseInt(process.env.PORT) || 3000,
  host: process.env.HOST || 'localhost',
  cors: {
    origin: process.env.CORS_ORIGIN ? process.env.CORS_ORIGIN.split(',') : ['http://localhost:3000', 'http://localhost:3001'],
    credentials: true
  },
  rateLimit: {
    windowMs: parseInt(process.env.RATE_LIMIT_WINDOW_MS) || 900000, // 15 minutes
    max: parseInt(process.env.RATE_LIMIT_MAX) || 100
  }
};

// Security configuration
const securityConfig = {
  bcryptRounds: parseInt(process.env.BCRYPT_ROUNDS) || 12,
  passwordMinLength: parseInt(process.env.PASSWORD_MIN_LENGTH) || 8,
  maxLoginAttempts: parseInt(process.env.MAX_LOGIN_ATTEMPTS) || 5,
  lockoutDuration: parseInt(process.env.LOCKOUT_DURATION) || 900000, // 15 minutes
  sessionSecret: process.env.SESSION_SECRET || 'fallback-secret-key',
  cookieMaxAge: parseInt(process.env.COOKIE_MAX_AGE) || 86400000 // 24 hours
};

// File upload configuration
const uploadConfig = {
  maxFileSize: parseInt(process.env.MAX_FILE_SIZE) || 5 * 1024 * 1024, // 5MB
  allowedTypes: process.env.ALLOWED_FILE_TYPES ? process.env.ALLOWED_FILE_TYPES.split(',') : ['image/jpeg', 'image/png', 'image/gif'],
  destination: process.env.UPLOAD_DEST || 'uploads/'
};

// Logging configuration
const loggingConfig = {
  level: process.env.LOG_LEVEL || 'info',
  format: process.env.LOG_FORMAT || 'combined',
  file: {
    enabled: process.env.LOG_FILE_ENABLED === 'true',
    filename: process.env.LOG_FILENAME || 'logs/app.log',
    maxSize: process.env.LOG_MAX_SIZE || '10m',
    maxFiles: parseInt(process.env.LOG_MAX_FILES) || 5
  }
};

// Configuration service class
class ConfigService {
  constructor() {
    this.database = databaseConfig;
    this.jwt = jwtConfig;
    this.cache = cacheConfig;
    this.email = emailConfig;
    this.sms = smsConfig;
    this.server = serverConfig;
    this.security = securityConfig;
    this.upload = uploadConfig;
    this.logging = loggingConfig;
  }

  // Helper functions
  get(key) {
    return process.env[key];
  }

  getDatabaseUrl() {
    return this.database.url;
  }

  getJwtSecret() {
    return this.jwt.secret;
  }

  getRedisConfig() {
    return redisConfig;
  }

  getCacheConfig() {
    return this.cache;
  }

  getEmailConfig() {
    return this.email;
  }

  getSmsConfig() {
    return this.sms;
  }

  getServerConfig() {
    return this.server;
  }

  getSecurityConfig() {
    return this.security;
  }

  getUploadConfig() {
    return this.upload;
  }

  getLoggingConfig() {
    return this.logging;
  }

  getPasswordPolicyConfig() {
    return {
      minLength: this.security.passwordMinLength || 8,
      maxLength: 128,
      bcryptRounds: this.security.bcryptRounds || 12,
      requireUppercase: true,
      requireLowercase: true,
      requireNumbers: true,
      requireSpecialChars: true,
      preventSequential: true,
      preventRepeated: true,
      preventPersonalInfo: true,
      bangladeshPatterns: true,
      minStrengthScore: 2
    };
  }

  getCORSConfig() {
    return {
      credentials: this.server.cors.credentials,
      methods: ['GET', 'POST', 'PUT', 'DELETE', 'OPTIONS'],
      allowedHeaders: ['Content-Type', 'Authorization', 'X-Requested-With'],
      exposedHeaders: ['X-Total-Count', 'X-Page-Count']
    };
  }

  // Environment helpers
  isDevelopment() {
    return process.env.NODE_ENV === 'development';
  }

  isProduction() {
    return process.env.NODE_ENV === 'production';
  }

  isTest() {
    return process.env.NODE_ENV === 'test';
  }
}

// Create singleton instance
const configService = new ConfigService();

// Export configuration objects and service
module.exports = {
  database: databaseConfig,
  jwt: jwtConfig,
  cache: cacheConfig,
  email: emailConfig,
  sms: smsConfig,
  server: serverConfig,
  security: securityConfig,
  upload: uploadConfig,
  logging: loggingConfig,
  
  // Helper functions
  getDatabaseUrl: () => databaseConfig.url,
  getJwtSecret: () => jwtConfig.secret,
  getRedisConfig: () => redisConfig,
  getCacheConfig: () => cacheConfig,
  getEmailConfig: () => emailConfig,
  getSmsConfig: () => smsConfig,
  getServerConfig: () => serverConfig,
  getSecurityConfig: () => securityConfig,
  getUploadConfig: () => uploadConfig,
  getLoggingConfig: () => loggingConfig,
  
  // Environment helpers
  isDevelopment: () => process.env.NODE_ENV === 'development',
  isProduction: () => process.env.NODE_ENV === 'production',
  isTest: () => process.env.NODE_ENV === 'test',
  
  // Config service instance
  configService
};
